body {
  background-color: rgba(248, 212, 205, 0.932);
  padding-bottom: 5%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.App-link:hover {
  color: #61dafb;
}

.btn {
  display: inline-block;
  border: none;
  background: rgb(13, 173, 247);
  color: #fff;
  font-weight: bold;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover {
  background: rgb(16, 38, 240);
}
.btn:selection {
  background: black;
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.editPanel {
  margin: 0;
}

.btnGroup {
}

.btn {
  margin: auto;
  background: lightblue;
  color: white;
}

.btn:hover {
  background-color: black;
  color: white;
}

.content {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.titleStyle {
}

.heads {
  width: 35%;
  margin-right: auto;
  padding-left: 5%;
  text-align: right;
  font-weight: bold;
}

.info {
  width: 40%;
  margin-left: auto;
  padding-right: 10%;
  text-align: left;
}

.fullBtn {
  width: 90%;
  max-width: 90%;
  background: rgba(250, 57, 40, 0.95);
  color: white;
  text-align: center;
  font-weight: bold;
  margin: auto;
  margin-top: 1%;
  margin-bottom: 1%;
  padding: 0%;
}

